.contact-container {
	@extend .other-container;

	content-visibility: auto;
}

.contact-inner {
	@extend .other-container-inner;

	// .contact-inner-wrapper
	&-wrapper {
		width: 100%;
		display: flex;
		flex-wrap: wrap;
		padding-left: $inner-padding-left;
		transition: padding-left 0.3s, padding-right 0.3s;

		@media (max-width: $mobile-break-point) {
			padding-left: $inner-padding-left-mobile;
		}
	}

	// .contact-inner-title
	&-title {
		font-size: 5rem;
		line-height: 1.1;
		font-weight: 900;
		letter-spacing: 6px;
		width: 70%;
		margin-bottom: 40px;

		@media (max-width: 1560px) {
			font-size: 4rem;
		}

		@media (max-width: 1200px) {
			width: 100%;
		}

		@media (max-width: $small-mobile-break-point) {
			font-size: 3rem;
		}

		// FadeRight
		@extend %transition-faderight-setup;

		transition-delay: 0.5s;

		.is-page-ready & {
			@extend %transition-faderight-active;
		}
	}

	// .contact-inner-column-row
	&-column-row {
		display: flex;
		width: 100%;

		@media (max-width: 1200px) {
			flex-wrap: wrap;
		}
	}
}

.contact-form {
	width: 100%;
	padding-right: 100px;

	@media (max-width: 1200px) {
		padding-right: 0;
	}

	// FadeRight
	@extend %transition-faderight-setup;

	transition-delay: 0.6s;

	.is-page-ready & {
		@extend %transition-faderight-active;
	}
}

.social-lists {
	width: 350px;
	min-width: 350px;
	margin-top: 15px;

	@media (max-width: 1200px) {
		width: 100%;
		min-width: auto;
		margin-top: 50px;
	}

	// FadeLeft
	@extend %transition-fadeleft-setup;

	transition-delay: 0.7s;

	.is-page-ready & {
		@extend %transition-fadeleft-active;
	}
}